.frame-list-item{
    color: inherit;
    display: flex;
    flex-direction: row;
    margin-bottom: 100px;
    text-decoration: none;
}
.frame-list-item:hover{
    color: inherit;
    text-decoration: none;
}
.frame-description{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 20px;
}
.title{
    font-size: 20px;
    margin-bottom: 0;
}
.frame-list-item label{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0;
}
p{
    margin: 0;
    padding: 0;
}
.frame-list-item input{
    width: 75px;
}
@media screen and (max-width: 430px) {
    .frame-list-item {
        flex-direction:  column;
    }
    .frame-description{
        margin-top: 20px;
        margin-left: 0;
    }
}