.shop-header{
    border-bottom: 1px solid #e5e5e5;
    display: flex;
    justify-content: space-between;
    margin-bottom: 1rem;
    flex: 0 0 auto;
}

.shop-header .logo{
    font-family: "Playfair Display", Georgia, serif;
    font-size:  2.5rem;
    padding-left: 1rem;
}
.shop-header .shopping-cart {
    align-self: center;
    font-size:  1.3rem;
    padding-right: 1rem;
}
.icon {
    font-size: 1.5rem;
    color: #4E5D6C;
    margin-right: 10px;
}
.additional-pages{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding-right: 1rem;
}
.logout {
    color: #5C4A2F;
    text-decoration: none;
    background-color: transparent;
    border: none;
    text-align: left;
    padding: 0;
}
.logout:hover{
    text-decoration: underline;
}
.logout:active {
    outline: none;
}